import React from "react";

const Card = ({ user, deletes,edit }) => {
    const {id, name,address,contact,p_address,email} = user;
    return (
        <>
            <div className="card" >
            <div className="info id">
                    <h3 style={{ display: "inline-block" }}>User Id :</h3>
                    <p style={{ display: "inline-block" }}>{id}</p>
                </div>
                <div className="info name">
                    <h3 style={{ display: "inline-block" }}>Full Name :</h3>
                    <p style={{ display: "inline-block" }}>{name}</p>
                </div>
                <div className="info Email">
                    <h3 style={{ display: "inline-block" }}>Email :</h3>
                    <p style={{ display: "inline-block" }}>{email}</p>
                </div>
                <div className="info contact">
                    <h3 style={{ display: "inline-block" }}>contact :</h3>
                    <p style={{ display: "inline-block" }}>{contact}</p>
                </div>
                <div className="info adress">
                    <h3 style={{ display: "inline-block" }}>Adress :</h3>
                    <p style={{ display: "inline-block" }}>{address}</p>
                </div>
                <div className="info p_adress">
                    <h3 style={{ display: "inline-block" }}>Permanent Adress :</h3>
                    <p style={{ display: "inline-block" }}>{p_address}</p>
                </div>
                <button className="edit-btn" onClick={() => edit(user)}>Edit</button>
                <button className="del-btn" onClick={() => deletes(user.code_user_id)}>delete</button>
            </div>

        </>
    )
}
export default Card;