import React, { useEffect, useState } from 'react';
const AddUser = (props) => {
    let btnStyle = {
        backgroundColor: "blue",
        width: "40%",
        marginTop: "10px",
        borderRadius: "10px"
    }
    let [error, setError] = useState("")
    let [fieldError, setFieldError] = useState("");
    let dataInput = (e) => {
        let value = e.target.value;
        let name = e.target.name;
        props.setEditUser((pre) => {
            if(name=="contact"){
                value=value.slice(0,11);
                return { ...pre, ["contact"]: value }
            }
            return { ...pre, [name]: value }
        });
        setFieldError("")
    }
    useEffect(() => {
        setError("");
        props.users.forEach((user) => {
            if (user.id == props.editUser.id && !props.editCheck) {
                setError("Enter Unique Id!")
            }
            if (user.code_user_id != props.editUser.code_user_id && user.id == props.editUser.id && props.editCheck) {
                setError("This Id already Exist!")
            }
        });
    }, [props.editUser]);
    let clearAll = () => {
        props.setEditUser(pre=>
            {
                return {code_user_id: "",
                id: "",
                name: "",
                email: "",
                contact: "",
                address: "",
                p_address: ""}
            }
        )
    }
    let handleSubmit = () => {
        if (props.editCheck) {
            if (error == "") {
                props.setUsers(pre => {
                    return pre.map((user) => {
                        if (user.code_user_id == props.editUser.code_user_id) {
                            return { ...props.editUser }
                        }
                        return user;
                    })
                })
                props.trigger(false);
                clearAll();
                props.setEditCheck(false)
            } else {
                alert("solve the error!")
            }

        } else if (!props.editCheck) {
            if (error != "") {
                alert("solve all error")
            } else if (props.editUser.id == "" || props.editUser.email == "" || props.editUser.contact == "" || props.editUser.address == "" || props.editUser.p_address == "") {
                setFieldError("Fill all fields")
            } else {
                props.setUsers(pre => [...pre, props.editUser]);
                props.trigger(false);
                clearAll();
                props.setEditCheck(false)
            }
        }
    }
    useEffect(()=>{
        localStorage.setItem("Users_List",JSON.stringify(props.users))
    },[props.users])
    return (
        <>
            <div className="add_popup">
                <div className='inner_popup'>
                    <div style={{ textAlign: "end" }}><button onClick={() => {
                        props.trigger(false);
                        clearAll();
                        props.setEditCheck(false)
                    }}></button></div>
                    <h2 style={{ textAlign: "center" }}>Add User</h2>
                    <p className='field_error'>{fieldError}</p>
                    <input autoComplete='off' onChange={dataInput} value={props.editUser.id} name="id"
                        type="number" placeholder='Enter User Id*' />
                    <p>{error}</p>
                    <input autoComplete='off' onChange={dataInput} value={props.editUser.name} name="name"
                        type="text" placeholder='Enter Name*' />
                    <input onChange={dataInput} value={props.editUser.email} name="email"
                        type="text" placeholder='Enter Email*' />
                    <input autoComplete='off' onChange={dataInput} value={props.editUser.contact} name="contact"
                        type="number" placeholder='Enter phone_no*' />
                    <input autoComplete='off' onChange={dataInput} value={props.editUser.address} name="address"
                        type="text" placeholder='Enter Current City*' />
                    <input autoComplete='off' onChange={dataInput} value={props.editUser.p_address} name="p_address"
                        type="text" placeholder='Enter Permanent Adress*' />
                    <div style={{ textAlign: "center" }}><button style={btnStyle} onClick={handleSubmit}>{props.editCheck ? "Edit" : "Add"}</button></div>
                </div>
            </div>
        </>
    )
}

export default AddUser;
