import React, { useEffect, useState } from "react";
import Card from "./Card";
import AddUser from "./AddUser";

let a = JSON.parse(localStorage.getItem("Users_List"));
const Home = (props) => {
    let [toogle, setToogle] = useState(false);
    let [users, setUsers] = useState(a);
    let [toogleState, setToogleState] = useState(false);
    let [filteredusers, setfilteredUsers] = useState([]);
    let [searching,setSearching]=useState({dropDownValue:"id",inputValue:""})
    let [editUser, setEditUser] = useState({
        code_user_id:"",
        id: "",
        name: "",
        email: "",
        contact: "",
        address: "",
        p_address: ""
    });
    let [editCheck, setEditCheck] = useState(false);
    const deleteFilter = (id) => {
        const filterData = users.filter(user => user.code_user_id !== id)
        return setUsers(filterData)
    }
    let tri = (tri) => {
        setToogle(tri)
    }
    let delelteAll = () => {
        setUsers([])
        setfilteredUsers([])
    }
    let search = (e) => {
        setSearching({...searching,inputValue: e.target.value});
    }
    useEffect(()=>{
        let filtered = users.filter((user) => user[searching.dropDownValue].toUpperCase().indexOf(searching.inputValue.toUpperCase()) != -1);
        setfilteredUsers([...filtered]);
        console.log(searching.inputValue)
        if (searching.inputValue.length > 0) {
            setToogleState(true)
        } else {
            setToogleState(false)
        }
        console.log("yes")
    },[searching])
    let searchWith = (e) => {
        setSearching({...searching,dropDownValue:e.target.value});
        console.log("hel")
    }
    let edit = (e) => {
        setEditUser(e);
        setToogle(true)
        setEditCheck(true)
    }
    return (
        <>
            <div style={{ display: toogle ? "block" : "none" }}>
                <AddUser editUser={editUser} setEditUser={setEditUser} trigger={tri} setUsers={setUsers} users={users} setEditCheck={setEditCheck} editCheck={editCheck} />
            </div>

            <div className="home_main">
                <h1>Home Screeen</h1>
                {/* Add information about user */}
                <div className="add_div">
                    <h2 style={
                        { display: "inline-block", marginLeft: "5px" }
                    }>Add User</h2>
                    <button className="add_icon" onClick={() => { setToogle(true);
                    setEditUser((pre) => {
                        return { ...pre,code_user_id:Date.now() }
                    });
                     }}>+</button>
                </div>
                {/* Total User counter */}
                {users.length > 0 ? <div style={{margin: "5px",overflow:"auto",padding:"5px"}}>
                    <h3 style={{display:"inline-block",float:"left"}}>Total Users</h3>
                    <h4 style={{display:"inline-block",float:"right"}}>{users.length}</h4>
                </div>: ""}
                {users.length > 0 ? <div className="del_all"><button className="all_btn" onClick={delelteAll}>Delete All</button></div> : ""}

                {/* search specific User  */}
                <div className="searchbar">
                    <input
                        type="text"
                        placeholder="Search"
                        onChange={search}
                    />
                    <select name="" id="dropdown" onChange={searchWith}>
                        <option value="id">by Id</option>
                        <option value="name">by name</option>
                        <option value="email">by email</option>
                        <option value="contact">by contact</option>
                        <option value="address">by address</option>
                        <option value="p_address">by Permanent address</option>
                    </select>
                </div>
                {/* Full data show */}
                <div className="full-info">
                    {
                        users.length != 0 ?
                            toogleState ? filteredusers.length != 0 ? filteredusers.map((user) => {
                                return <Card key={user.code_user_id} user={user} edit={edit} deletes={deleteFilter} />
                            }) : <p style={{ textAlign: "center", marginTop: "15px" }}>NoData found!</p>
                                : users.map((user) => {
                                    return <Card key={user.code_user_id} user={user} edit={edit} deletes={deleteFilter} />
                                }) : <p style={{ textAlign: "center", marginTop: "15px" }}>NoData found!</p>
                    }
                </div>

            </div>

        </>
    )
}
export default Home;